.App {
  font-family: Arial,;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}
.HeaderStuff{
  
  color: blue;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.goalHoursInput{
  width: 30px;
  padding: 4px;
}
.timeInput{
  width: 50px;
  padding: 4px;
}
.gradient {
  opacity: .2;
  background-color: gray;
  background-image:
    linear-gradient(
      90deg,
      gray,
      white 10%
    );
}
.paddingleftRight{
  margin-right: 4px;
  margin-left: 4px;
  font-size: large;
}
.top{
  font-size: large;
}
.bottom{
  font-size: large;
}
.output{
  color: green;
  font-size: x-large;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
